import { useEffect, useState } from "react";

import "react-responsive-carousel/lib/styles/carousel.min.css"; // requires a loader
import { Carousel } from "react-responsive-carousel";

const starItem = (id) => {
  return (
    <li key={id}>
      <a href="#" rel="noreferrer">
        <i className="fa fa-star"></i>
      </a>
    </li>
  );
};

const renderStar = (count) => {
  let rows = [];
  for (var i = 0; i <= count - 1; i++) {
    rows.push(starItem(i));
  }
  return rows;
};

const renderConainer = (item) => {
  if (item === undefined) return <div />;

  return (
    <div key={item.id} className="col-md-6">
      <div className="test_inner">
        <div className="testimonial_shape"></div>
        <div className="test_img">
          <img src={`${item.image.url}`} className="img-responsive" alt="img" />
        </div>
        <div className="testimonial_inner">
          <h3>{item.name}</h3>
          <h5>{item.title}</h5>
          <p>{item.description}</p>
          <ul>{renderStar(item.stars)}</ul>
          <span>{item.name}</span>
        </div>
      </div>
    </div>
  );
};

export default function testimonials({ data }) {
  const [testimonials, setTestimonials] = useState([]);

  useEffect(() => {
    let newData = [];
    let count = 0;
    for (var i = 0; i < data.length; ) {
      newData.push({ id: count++, values: [data[i++], data[i++]] });
    }
    setTestimonials(newData);
  }, [data]);

  return (
    <>
      <section id="testi" className="common">
        <div className="container">
          <div className="row">
            <div className="col-md-12">
              <div className="heading text-center">
                <h4>What Think Client About Us?</h4>
                <h2>Testimonial</h2>
              </div>
            </div>
          </div>
        </div>
      </section>
      <section className="testimonial">
        <div className="container">
          <div className="row">
            <Carousel autoPlay infiniteLoop>
              {testimonials.map((item) => (
                <div key={item.id}>
                  {renderConainer(item.values[0])}
                  {renderConainer(item.values[1])}
                </div>
              ))}
            </Carousel>
          </div>
        </div>
      </section>
    </>
  );
}
